
.container {
  border: 2px solid #fff;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 4vmin;
  padding: 10px 25px;
  text-decoration: none;
}
.container:hover, .container:focus {
  background-color: rgba(255, 255, 255, 0.15);
  color: #fff;
}
.link {
  background: transparent;
  border: none;
  color: #000;
}



